.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: blue;
    color: #fff;
    padding: 10px;
}

.navbar-title {
    font-size: 24px;
}

.navbar-links {
    list-style-type: none;
    display: flex;
}

.navbar-link-item {
    margin-right: 10px;
    cursor: pointer;
}

.navbar-link-item:hover {
    text-decoration: underline;
}
.link{
    color: white;
    text-decoration: none;
}