.Home {
    text-align: center;
    margin: 0 auto;
    padding: 20px;
}

.Home-header {
    background-color: #f5f5f5;
    padding: 20px;
}

h1 {
    font-size: 32px;
    margin-bottom: 10px;
}

p {
    margin-bottom: 10px;
}

.cta-button {
    background-color: #3498db;
    color: #fff;
    border: none;
    padding: 10px 20px;
    margin-top: 20px;
    font-size: 18px;
    cursor: pointer;
}

.about-section {
    margin-top: 40px;
}

h2 {
    font-size: 24px;
    margin-bottom: 10px;
}

.benefits-section {
    margin-top: 40px;
}

ul {
    list-style-type: disc;
    text-align: left;
    margin-left: 30px;
    margin-bottom: 10px;
}

.testimonial-section {
    margin-top: 40px;
}

.testimonial {
    margin-bottom: 20px;
    text-align: left;
}

footer {
    background-color: #f5f5f5;
    padding: 10px;
    margin-top: 40px;
    font-size: 14px;
    color: #777;
}