
 form {
     display: flex;
     flex-direction: column;
     gap: 10px;
     max-width: 300px;
     margin: 0 auto;
     margin-bottom: 20px;
     align-items: center;
 }
label {
    font-weight: bold;
}

input[type="text"],
input[type="email"],
input[type="date"],
input[type="number"],
select {
    padding: 5px;
    border-radius: 3px;
    border: 1px solid #ccc;
}

button {
    padding: 10px;
    background-color: #3f51b5;
    color: #fff;
    border: none;
    border-radius: 3px;
    cursor: pointer;
}

p {
    color: red;
    margin-top: 10px;
}



button[type="reset"],
button[type="submit"] {
    padding: 10px 20px;
    background-color: #3f51b5;
    color: #fff;
    border: none;
    border-radius: 3px;
    cursor: pointer;
}
h2{
    text-align: center;
}